.faq {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	font-family: $poppins;
	align-items: flex-end;
	margin-block: 7rem;
	margin-inline: 13.5%;

	&__left {
		width: 50%;
	}

	&__right {
		width: 50%;
		text-align: center;
	}

	&__title {
		font-size: 6rem;
		margin-top: 2rem;
		margin-bottom: 3rem;
		font-family: $roboto;
	}

	&__subtitle {
		font-size: 1.8rem;
		margin-bottom: 4rem;
	}

	&__accordion {
		margin-top: 10px;

		&__question {
			color: $primaryColorLight;
			font-weight: 600;
			font-size: 2.4rem;
			margin-bottom: 2rem;
			cursor: pointer;
		}

		&__answer {
			font-size: 1.8rem;
			margin-bottom: 2rem;
			display: none;
		}
	}
}

.answer--open {
	display: block;
}

@media (max-width: 768px) {
	.faq {
		&__left {
			width: 100%;
		}

		&__questionMark {
			max-width: 30%;
		}
	}
}
