/**** Colors ****/
/**** Fonts ****/
/**** Images ****/
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.1/css/all.min.css");
@font-face {
  font-family: "Roboto", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap");
}
@font-face {
  font-family: "Poppins", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Poppins:wght@600&family=Roboto&display=swap");
}
/**** Global ****/
html,
body {
  width: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
  font-size: 10px;
}
html img,
body img {
  max-width: 100%;
}

@media (max-width: 1366px) {
  html,
body {
    font-size: 5px;
  }
}
/**** Components ****/
.btn {
  font-size: 2.4rem;
  font-family: "Roboto", sans-serif;
  padding: 2rem;
  background: #ff4363;
  color: white;
  border: 3px solid #ff4363;
  border-radius: 90px;
  transition: 0.6s;
  width: fit-content;
  font-weight: 300;
  cursor: pointer;
}
.btn i {
  margin-right: 5px;
}
.btn:hover {
  background: white;
  color: #ff4363;
  transition: 0.6s;
  border: 3px solid #ff4363;
}

.btn--white {
  background: white;
  color: #ff4363;
}
.btn--white:hover {
  background: #ff4363;
  color: white;
  border: 3px solid white;
}

.btn--transparent {
  background: transparent;
}

.hero {
  box-sizing: border-box;
  width: 100vw;
  min-height: 106vh;
  background-image: url("./img/heroBackground.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 2rem;
}
.hero__content {
  padding-top: 11%;
  padding-left: 13.5%;
  width: 40%;
  color: white;
}
.hero__title {
  font-family: "Roboto", sans-serif;
  font-size: 8.2rem;
  font-weight: 700;
  color: white;
  line-height: 10rem;
  margin-bottom: 3rem;
}
.hero__p {
  margin-top: 0;
  font-size: 1.8rem;
  line-height: 3rem;
}
.hero__buttonsWrapper {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 70%;
}
.hero__buttonsWrapper .btn--transparent {
  margin-left: 30px;
  font-size: 1.8rem;
  font-weight: bold;
}

@media (max-width: 480px) {
  .hero {
    background-size: 100%;
    background-position: 50% 106%;
    background-color: #ff4363;
    min-height: 100vh;
  }
  .hero__content {
    width: 90%;
    padding-inline: 0;
    margin-top: 0;
    padding-top: 20%;
    margin-inline: 5%;
  }
  .hero__buttonsWrapper {
    width: 100%;
  }
}
.nav {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  background: white;
  width: 0;
  transition: 0.5s;
}
.nav .menu {
  margin-top: 20%;
  font-size: 2em;
  list-style-type: none;
}
.nav .menu-item {
  line-height: 2em;
  transition: 0.3s;
}
.nav .menu-item:hover {
  color: #ff4363;
  font-size: 1.3em;
  font-weight: bold;
  transition: 0.3s;
}
.nav .menu a {
  color: #ff7588;
  text-decoration: none;
}
.nav__closeBtn {
  display: none;
  width: fit-content;
  float: right;
  margin-top: 30px;
  margin-right: 50px;
  transition: 0.5s;
  cursor: pointer;
}
.nav__closeBtn:hover {
  transform: rotate(90deg);
  transition: 0.5s;
}
.nav__closeBtn i {
  font-size: 4em;
  color: #ff4363;
}
.nav--active {
  width: clamp(200px, 20%, 60%);
  transition: 0.5s;
}
.nav--active .nav__closeBtn {
  display: block !important;
}

.features {
  font-family: "Poppins", sans-serif;
  display: flex;
  flex-wrap: wrap;
  place-content: center;
  place-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 9rem;
  gap: 3rem;
}
.features .featuresBox {
  text-align: center;
  width: 23%;
  padding-top: 11rem;
  box-sizing: border-box;
  box-shadow: 0px 0px 30px 0px #707070;
  border-radius: 10px;
}
.features .featuresBox__title {
  font-size: 2.4rem;
  margin-top: 1.8em;
  font-weight: 700;
  margin-bottom: 10px;
}
.features .featuresBox__p {
  font-size: 1.6em;
  line-height: 3em;
  width: 70%;
  margin-inline: auto;
  margin-top: 0;
}

@media (max-width: 768px) {
  .features .featuresBox {
    width: 70%;
  }
}
.learnMore {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 9rem;
  font-family: "Poppins", sans-serif;
}
.learnMore__right {
  padding-top: 3rem;
  max-width: 50%;
  box-sizing: border-box;
  padding-inline: 5%;
}
.learnMore__title {
  font-family: "Roboto", sans-serif;
  font-size: 6rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}
.learnMore__p {
  font-size: 1.8em;
  line-height: 3rem;
}
.learnMore__list {
  font-size: 1.8em;
  padding-left: 0;
  line-height: 4.7rem;
  margin-top: 2.5rem;
}
.learnMore__listItem {
  list-style-type: none;
}
.learnMore__listItem::before {
  content: "";
  background: #ff7588;
  display: inline-block;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 100%;
  margin-right: 1em;
}
.learnMore__button {
  font-family: "Roboto", sans-serif;
  font-size: 2.4rem;
  font-weight: bold;
  padding: 0.8em 1.6em;
  margin-top: 0.8em;
}

@media (max-width: 720px) {
  .learnMore__icon {
    max-width: 20%;
  }
  .learnMore__left {
    width: 60%;
  }
  .learnMore__right {
    max-width: 100%;
  }
}
.faq {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  align-items: flex-end;
  margin-block: 7rem;
  margin-inline: 13.5%;
}
.faq__left {
  width: 50%;
}
.faq__right {
  width: 50%;
  text-align: center;
}
.faq__title {
  font-size: 6rem;
  margin-top: 2rem;
  margin-bottom: 3rem;
  font-family: "Roboto", sans-serif;
}
.faq__subtitle {
  font-size: 1.8rem;
  margin-bottom: 4rem;
}
.faq__accordion {
  margin-top: 10px;
}
.faq__accordion__question {
  color: #ff7588;
  font-weight: 600;
  font-size: 2.4rem;
  margin-bottom: 2rem;
  cursor: pointer;
}
.faq__accordion__answer {
  font-size: 1.8rem;
  margin-bottom: 2rem;
  display: none;
}

.answer--open {
  display: block;
}

@media (max-width: 768px) {
  .faq__left {
    width: 100%;
  }
  .faq__questionMark {
    max-width: 30%;
  }
}
.statistics {
  color: white;
  width: 73%;
  margin-block: 6%;
  margin-inline: auto;
}
.statistics .statistics__container {
  background: #ff4363;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  box-sizing: border-box;
  padding-inline: 8.5%;
  border-radius: 10px;
  padding-bottom: 3rem;
}
.statistics .statisticsBox {
  box-sizing: border-box;
  padding: 3em 0;
  text-align: center;
}
.statistics .statisticsBox__title {
  font-size: 6em;
  font-family: "Roboto", sans-serif;
  margin-top: 6rem;
  margin-bottom: 0;
}
.statistics .statisticsBox__desc {
  font-size: 1.8em;
  font-weight: bold;
}

@media (max-width: 720px) {
  .statistics {
    width: 100%;
    font-size: 3px;
  }
}
.contactUs {
  width: 40%;
  margin-inline: auto;
  text-align: center;
}
.contactUs__title {
  font-size: 6rem;
  font-family: "Roboto", sans-serif;
  margin-bottom: 3rem;
}
.contactUs__p {
  font-size: 1.8rem;
  font-family: "Poppins", sans-serif;
}
.contactUs .btn {
  margin-top: 1.5rem;
  font-size: 2.4rem;
  padding: 2rem 4rem;
}

.footer {
  background: black;
  font-family: "Poppins", sans-serif;
  color: white;
  margin-top: 10rem;
  padding: 2.5rem 0;
  font-size: 1.8rem;
}
.footer a {
  color: white;
}
.footer a:hover {
  color: #ff4363;
}
.footer__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 73%;
  margin-inline: auto;
}
.footer__socials {
  font-size: 2.5rem;
}
.footer__socials i {
  margin-right: 5rem;
}
.footer__socials i:last-of-type {
  margin-right: 0;
}
.footer__logo {
  width: 14%;
}
.footer__logo img {
  width: 65%;
}
.footer__copyright {
  font-weight: bold;
}

@media (max-width: 768px) {
  .footer__socials {
    font-size: 1.5rem;
  }
  .footer__logo {
    display: none;
  }
  .footer__copyright {
    font-size: 1.5rem;
  }
}
.header {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 73%;
  margin-inline: 13.5%;
  margin-block: 2rem;
}
.header__hamburger {
  cursor: pointer;
}